@import url('https://fonts.googleapis.com/css2?family=Hedvig+Letters+Serif:opsz@12..24&display=swap');
.mobile{
  display: none;
}
.container {
  display: grid;
  grid-template-columns: repeat(14,1fr);
  grid-template-rows: repeat(12,50px);
}
.info{
  color:#ffffff;
  grid-column-start: 3;
  grid-column-end: 9;
  grid-row-start: 6;
  z-index: 2;
}
.info h1{
  font-size: 360%;  
}
.info p{
  color: #aaaaaa;
  font-weight: 200;
  padding-top: 10px;
  font-size: 120%;  
  line-height: 25px;
}
.pfp {  
  grid-column-start: 10;
  grid-row-start: 3;
  width: 300%;
  border-radius: 20px;
}
.profile-rectangle {
  grid-row-start: 3;
  grid-column-start: 10;
  width: 300%;
  height: 430px;
  border-radius: 20px;
  background-color: #696969;
  margin-top: 30%;
  margin-left: 30%;
}
.icons {  
  color: #000000;
  grid-column-start: 3;
  grid-column-end: 8;
  grid-row-start: 10;
  z-index: 2;
}
.container1 {
  display: inline-flex;
  justify-content: center;
  grid-column-start: 1;
  grid-column-end: 7;
  grid-row-start: 2;
  grid-template-columns: 200px 200px 200px 100px 100px;
  grid-template-rows: 30px 20px;
  width: 30px;
  height: 30px;
  padding: 10px;
  border-radius: 10%;
  box-shadow: 3px 10px 3px rgb(153, 153, 153);
  background-color: #ffffff;
  margin-right: 30px;
}
.container1:hover{
  background-color: #9c85ab;
}
@media (max-width: 430px) {
  .navbar {
    font-size: 100%;
  }
  .container {
    display: none;
  }
  .mobile{
    display: block;
  }
  .infoM{
    color:#0d0d0d;
    text-align: center;
    margin-top: 20%;
  }
  .infoM h1{
    font-size: 200%;  
    color: white;
  }
  .infoM p{
    padding-top: 10px;
    font-size: 80%;  
    text-align: center;
    margin-left: 15%;
    margin-right: 15%;
    color: #aaaaaa;
  }
  .pfpM {  
    margin-left: auto;
    margin-right: auto;
    margin-top: 7%;
    z-index: 2;
    width: 50%;
    margin-left: 25%;
    position: absolute;
    z-index: 2;
    border-radius: 20px;
  }
  .profile-rectangleM {
    margin-left: 30%;
    margin-top: 10%;
    width: 50%;
    height: 250px;
    border-radius: 20px;
    background-color: #696969;
    position: relative;
    z-index: 1;
  }
  .iconsM{
    /* display: none; */
    color: #86679a;
    z-index: 2;
    display: block;
    margin-left: 35%;
    margin-right: auto;
    margin-top: 10%;
  }
  .iconsM:hover{
    color: #86679a;
  } 
  .containerM {
    margin-right: 30px;
    width: 10px;
    height: 10px;
    padding: 10px;
    border-radius: 10%;
    box-shadow: 3px 10px 3px rgb(153, 153, 153);
    background-color: #ffffff;
  }
  .containerM:hover {
    background-color: #A356D6;
  }
}